aside {
  width: 250px;
  box-shadow: 0 0 10px #ddd;
  position: fixed !important;
  top: 0;
  bottom: 0;
  z-index: 999;
}

aside > h3 {
  margin: 0 auto 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

@media (min-width: 767px) {
  aside > h3::before,
  aside > h3::after {
    content: "";
    position: absolute;
    background: black;
    left: 50%;
    transform: translateX(-50%);
  }

  aside > h3::before {
    width: calc(100% + 20px);
    height: 2px;
    bottom: -15px;
  }

  aside > h3::after {
    border-radius: 50%;
    width: 12px;
    height: 12px;
    bottom: -24px;
    border: 4px solid white;
  }
}

aside ul li a {
  transition: 0.3s;
  margin-bottom: 5px;
}

aside ul li a:hover,
aside ul li a.active {
  background: #f6f6f6;
}

aside ul li a span {
  margin-left: 10px;
  font-size: 14px;
}
@media (max-width: 767px) {
  .page aside {
    width: 58px;
    padding: 10px;
  }
  aside > h3 {
    font-size: 13px;
    margin-bottom: 15px;
  }
}

.dark-mode aside {
  background-color: #121212;
  color: #ffffff;
  box-shadow: 0 0 10px #2c2c2c;
}

.dark-mode a {
  color: #fff;
}

.dark-mode aside ul li a:hover, .dark-mode ul li a.active {
  background: #2c2c2c;
}